/* RangeSwitcherChart.css - Modo Oscuro Mejorado CON COLORES DINÁMICOS */
@import url('https://api.fontshare.com/v2/css?f[]=satoshi@700,900&display=swap');

.range-switcher-chart {
  width: 90%;
  height: 55.5vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  position: relative; 
  background-color: #181818 !important;
  border: 1px solid #333333;
  border-radius: 0 0 16px 16px;
}

/* ===== CHART TYPE SWITCHER STYLES (ABAJO) ===== */
.chart-type-switcher {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 12px;
  background-color: #181818;
  width: 100%;
  height: 60px;
  border-radius: 0 0 16px 16px;
  position: relative;
  overflow: visible;
  z-index: 20;
}

.chart-type-button {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 8px 16px;
  border-radius: 12px;
  border: 1px solid #333333;
  font-family: 'Satoshi', Arial, sans-serif;
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  background-color: #1E1E1E;
  color: #CCCCCC;
  height: 40px;
  width: 29%; 
  gap: 4px;
  position: relative;
  overflow: hidden;
  z-index: 21;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.4);
  pointer-events: auto;
}

.chart-type-button:disabled {
  cursor: not-allowed;
  opacity: 0.6;
}

/* BOTONES ACTIVOS - COLORES DINÁMICOS POR MERCADO */
/* S&P 500 - Azul */
.section1 .chart-type-button.active,
.section1 .range-button.active {
  background-color: #007ACC;
  color: #FFFFFF;
  box-shadow: 0 0 8px rgba(0, 122, 204, 0.6);
  transform: translateY(-2px);
}

.section1 .chart-type-button:not(.active):not(:disabled):hover,
.section1 .range-button:not(.active):not(:disabled):hover {
  background-color: rgba(0, 122, 204, 0.1);
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(0, 122, 204, 0.3);
}

/* IBEX 35 - Naranja */
.section2 .chart-type-button.active,
.section2 .range-button.active {
  background-color: #FF6B35;
  color: #FFFFFF;
  box-shadow: 0 0 8px rgba(255, 107, 53, 0.6);
  transform: translateY(-2px);
}

.section2 .chart-type-button:not(.active):not(:disabled):hover,
.section2 .range-button:not(.active):not(:disabled):hover {
  background-color: rgba(255, 107, 53, 0.1);
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(255, 107, 53, 0.3);
}

/* NASDAQ - Verde */
.section3 .chart-type-button.active,
.section3 .range-button.active {
  background-color: #00D4AA;
  color: #FFFFFF;
  box-shadow: 0 0 8px rgba(0, 212, 170, 0.6);
  transform: translateY(-2px);
}

.section3 .chart-type-button:not(.active):not(:disabled):hover,
.section3 .range-button:not(.active):not(:disabled):hover {
  background-color: rgba(0, 212, 170, 0.1);
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(0, 212, 170, 0.3);
}

/* VIX - Rojo */
.section4 .chart-type-button.active,
.section4 .range-button.active {
  background-color: #00CED1;
  color: #FFFFFF;
  box-shadow: 0 0 8px rgba(0, 206, 209, 0.6);
  transform: translateY(-2px);
}

.section4 .chart-type-button:not(.active):not(:disabled):hover,
.section4 .range-button:not(.active):not(:disabled):hover {
  background-color: rgba(255, 71, 87, 0.1);
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(255, 71, 87, 0.3);
}

.chart-type-label {
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.8px;
  transition: all 0.3s ease;
}

.chart-type-button.active .chart-type-label {
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
}

/* Ripple effect para chart type buttons */
.chart-type-button::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.3);
  transform: translate(-50%, -50%);
  transition: width 0.6s ease, height 0.6s ease;
}

.chart-type-button:active::before {
  width: 300px;
  height: 300px;
}

/* ===== RANGE SWITCHER STYLES (ARRIBA) ===== */
.range-switcher-header {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  background-color: #181818;
  border-radius: 16px 16px 0 0;
  min-height: 60px;
  padding: 0 20px;
}

.range-button {
  padding: 8px 16px;
  border-radius: 8px;
  border: 1px solid #333333;
  font-size: 12px;
  font-weight: 700;
  font-family: 'Satoshi', Arial, sans-serif;
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  background-color: #1E1E1E;
  color: #CCCCCC;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  position: relative;
  overflow: hidden;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
}

.range-button:disabled {
  cursor: not-allowed;
  opacity: 0.6;
}

/* Ripple effect para range buttons */
.range-button::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.3);
  transform: translate(-50%, -50%);
  transition: width 0.6s ease, height 0.6s ease;
}

.range-button:active::before {
  width: 300px;
  height: 300px;
}

/* ===== CHART CONTAINER ===== */
.chart-container {
  flex: 1;
  width: 100%;
  position: relative;
  background-color: #181818;
  overflow: hidden;
}

.loading-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 14px;
  color: #007ACC;
  font-family: 'Satoshi', Arial, sans-serif;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 12px;
  background-color: #202020;
  padding: 16px 24px;
  border-radius: 6px;
  backdrop-filter: blur(10px);
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.4);
  border: 1px solid #444444;
}

.loading-spinner {
  width: 20px;
  height: 20px;
  border: 2px solid rgba(0, 122, 204, 0.3);
  border-top: 2px solid #007ACC;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { 
    transform: rotate(0deg); 
  }
  100% { 
    transform: rotate(360deg); 
  }
}

/* Error state */
.error-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 14px;
  color: #FF4D4F;
  font-family: 'Satoshi', Arial, sans-serif;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 12px;
  background-color: #202020;
  padding: 16px 24px;
  border-radius: 6px;
  backdrop-filter: blur(10px);
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.4);
  border: 1px solid #444444;
}

/* ===== RESPONSIVE DESIGN ===== */
@media (max-width: 768px) {
  .chart-type-switcher {
    height: 55px;
    gap: 8px;
  }
  .chart-container {
    touch-action: pan-x !important;
    user-select: none !important;
  }
  
  .chart-container canvas {
    touch-action: pan-x !important;
  }
  
  .chart-type-button {
    min-width: 50px;
    height: 35px;
    padding: 6px 12px;
  }
  
  .chart-type-label {
    font-size: 10px;
  }
  
  .range-switcher-header {
    min-height: 55px;
    gap: 8px;
    padding: 0 15px;
  }
  
  .range-button {
    padding: 6px 12px;
    font-size: 11px;
  }
}

/* Ocultar posibles marcas de agua o logos de TradingView */
.tv-lightweight-charts [class*="watermark"],
.tv-lightweight-charts [class*="logo"],
.tv-lightweight-charts [class*="trademark"],
.tv-lightweight-charts [class*="attribution"] {
  display: none !important;
  visibility: hidden !important;
  opacity: 0 !important;
}

.tv-lightweight-charts div[data-name*="tradingview"],
.tv-lightweight-charts span[data-name*="tradingview"],
.tv-lightweight-charts a[href*="tradingview"] {
  display: none !important;
}

.tv-lightweight-charts div[style*="position: absolute"][style*="bottom"],
.tv-lightweight-charts div[style*="position: absolute"][style*="right"] {
  display: none !important;
}

.tv-lightweight-charts *:contains("TradingView"),
.tv-lightweight-charts *:contains("tradingview.com") {
  display: none !important;
}

.tv-lightweight-charts div {
  font-size: inherit !important;
}

.tv-lightweight-charts div[style*="font-size: 10px"],
.tv-lightweight-charts div[style*="font-size: 11px"],
.tv-lightweight-charts div[style*="font-size: 12px"] {
  display: none !important;
}

.tv-lightweight-charts div[style*="z-index: 999"],
.tv-lightweight-charts div[style*="z-index: 9999"] {
  display: none !important;
}

.tv-lightweight-charts canvas + div,
.tv-lightweight-charts canvas + span {
  display: none !important;
}